import Component from '../component';
import { getDeviceValue } from '../helpers';

class FileVideo extends Component {
  setup() {
    this.$player = this.$element;
    this.player = this.$element.get(0);
    this.$source = this.$player.find('source');
    this.handleClick = this.handleClick.bind(this);

    if(!this.$player.attr('poster')) {
      const poster = getDeviceValue({
        desktop: this.$player.data('poster-desktop'),
        laptop: this.$player.data('poster-laptop'),
        tablet: this.$player.data('poster-tablet'),
        phone: this.$player.data('poster-phone')
      });
      this.$player.attr('poster', poster);
    }

    if(!this.$source.attr('src')) {
      const url = getDeviceValue({
        desktop: this.$source.data('desktop'),
        laptop: this.$source.data('laptop'),
        tablet: this.$source.data('tablet'),
        phone: this.$source.data('phone')
      });
      this.$source.attr('src', url);
      this.player.load();
    }
  }

  load(loadOptions = {}) {
    if(loadOptions.autoplay == true) {
      this.player.play();
    }
    this.player.onplay = () => {
      this.$element.trigger('playing.video');
    }
    this.player.onpause = () => {
      this.$element.trigger('paused.video');
    }
  }

  listen() {
    if (!this.options.background && !this.player.controls) {
      this.$element.on('click', this.handleClick);
    }

    this.$element.on('clicked-play-button.video', () => {
      if (!this.options.muted) {
        this.player.muted = false;
      }
      this.play();
    });
  }

  handleClick() {
    if(this.player.paused == true) {
      this.play();
    } else {
      this.pause();
    }
  }

  play() {
    this.player.play();
  }

  pause() {
    this.player.pause();
  }

  unmute() {
    this.player.muted = false; 
  }

  destroy() {
    this.$element.off('click', this.handleClick);
    this.pause();
    this.player.currentTime = 0;
    this.player = null;
  }
}

FileVideo.slug = 'video';

export default FileVideo;
