import Component from '../component';
import YouTubeVideo from './youtube-video';
import VimeoVideo from './vimeo-video';
import FileVideo from './file-video';

class Video extends Component {
  setup() {
    const {
      url,
      mode,
    } = this.options;
    this.url = url || this.$element.data('video-url');
    this.mode = mode || this.$element.data('video-mode');
    this.$placeholderButton = this.$find('placeholder-button');
    this.$playButton = this.$find('play-button');
    this.$unmuteButton = this.$find('unmute-button');
    this.handlePlayingVideo = this.handlePlayingVideo.bind(this);
    this.handlePausedVideo = this.handlePausedVideo.bind(this);
    this.handleEndedVideo = this.handleEndedVideo.bind(this);
    this.handlePlaceholderClick = this.handlePlaceholderClick.bind(this);
    this.handleLoadedExternalVideo = this.handleLoadedExternalVideo.bind(this);
    this.handlePlayButtonClick = this.handlePlayButtonClick.bind(this);
    this.handleUnmuteButtonClick = this.handleUnmuteButtonClick.bind(this);
  }

  listen() {
    this.$element.on('playing.video', this.handlePlayingVideo);
    this.$element.on('paused.video', this.handlePausedVideo);
    this.$element.on('ended.video', this.handleEndedVideo);
    this.$placeholderButton.on('click', this.handlePlaceholderClick);
    this.$element.on('loaded-external.video', this.handleLoadedExternalVideo);
    this.$playButton.on('click', this.handlePlayButtonClick);
    this.$unmuteButton.on('click', this.handleUnmuteButtonClick);
    this.initialize();
  }

  initialize() {
    const {
      url,
      mode,
      ...videoOptions
    } = this.options;
    let VideoComponent;
    if (!this.url || (typeof this.url === 'string' && this.url.length === 0)) {
      VideoComponent = FileVideo;
    } else if (VimeoVideo.handlesUrl(this.url)) {
      VideoComponent = VimeoVideo;
    } else if (YouTubeVideo.handlesUrl(this.url)) {
      VideoComponent = YouTubeVideo;
    }
    if (VideoComponent) {
      const $target = this.url ? this.$element : this.$children('player');
      this.video = new VideoComponent($target, {
        background: this.mode == 'background',
        ...videoOptions
      });
      if (this.$placeholderButton.length == 0) 
        this.video.load();
    } else
      throw `The video component can't handle the provided video`;
  }

  handlePlayingVideo() {
    this.$element.removeClass('video--loading');
    this.$element.removeClass('video--paused');
    this.$element.addClass('video--playing');
    this.$unmuteButton.removeClass('hidden');
  }

  handlePausedVideo() {
    this.$element.removeClass('video--playing');
    this.$element.addClass('video--paused');
  }

  handleEndedVideo() {
    this.$element.removeClass('video--playing');
  }

  handlePlaceholderClick() {
    if (this.video) {
      this.$placeholderButton.remove();
      if(this.$playButton.length) {
        this.$playButton.remove();
      }
      this.video.load({
        autoplay: true,
      });
    }
  }

  handleLoadedExternalVideo() {
    if(this.$playButton.length) {
      this.$playButton.remove();
    }
  }

  handlePlayButtonClick() {
    if (this.video) {
      this.$playButton.remove();
      if (this.$placeholderButton.length) {
        this.$placeholderButton.remove();
      }
      this.video.$element.trigger('clicked-play-button.video');
    }
  }

  handleUnmuteButtonClick() {
    this.$unmuteButton.addClass('hidden');
    this.unmute();
  }

  play() {
    this.video.play();
  }

  pause() {
    this.video.pause();
  }

  unmute() {
    this.video.unmute();
  }

  destroy() {
    this.$element.off('playing.video', this.handlePlayingVideo);
    this.$element.off('paused.video', this.handlePausedVideo);
    this.$element.off('ended.video', this.handleEndedVideo);
    this.$placeholderButton.off('click', this.handlePlaceholderClick);
    this.$element.off('loaded-external.video', this.handleLoadedExternalVideo);
    this.$playButton.off('click', this.handlePlayButtonClick);
    this.video.destroy();
    this.video = null;
  }
}

Video.slug = 'video';

export default Video;