import $ from 'jquery';
const easeOutCubic = (t, b, c, d) => c * ((t = t/d - 1) * t * t + 1) + b;

export const smoothScrollTo = (top, scrollDuration, callback) => {
  let abort = false;
  const finalY = Math.min(Math.ceil(top), document.documentElement.scrollHeight - window.innerHeight);
  const originalY = Math.ceil(window.scrollY);
  const offset = Math.ceil(finalY - originalY);
  const initialTimestamp = performance.now();
  const step = (newTimestamp) => {
    const nextY = Math.ceil(easeOutCubic(newTimestamp - initialTimestamp, originalY, offset, scrollDuration));
    if (abort || (finalY >= originalY && nextY >= finalY) || (finalY <= originalY && nextY <= finalY + 1)) {
      window.scrollTo(0, finalY);
      if(callback)
        callback();
      return;
    } else {
      window.scrollTo(0, nextY);
    }
    window.requestAnimationFrame(step);
  }
  window.requestAnimationFrame(step);
  return () => { 
    abort = true;
  };
};

const minWidthByDevice = {
  desktop: 1441,
  laptop: 1025,
  tablet: 768,
  phone: 0,
};
export const getDevice = (rules = minWidthByDevice) => {
  const deviceWidth = window.innerWidth;
  return Object.keys(rules).find(
    (device, index, devices) => (
      index === devices.length - 1
      || deviceWidth >= minWidthByDevice[device] 
    )
  );
};
export const getDeviceValue = values => values[getDevice(values)];
export const rem = value => value * parseFloat(getComputedStyle(document.documentElement).fontSize);
export const vw = value => value * parseFloat(window.innerWidth) / 100.0;

export const watchIfVisible = (element, callback) => {
  execIfVisible(element, callback);
  const handleScroll = () => {
    if(execIfVisible(element, callback))
      $(window).off('scroll', handleScroll);
  };
  $(window).on('scroll', handleScroll);
};

export const execIfVisible = (element, callback) => {
  const rect = element.get(0).getBoundingClientRect();
  if (rect.top > -rect.height && rect.top < window.innerHeight - rem(4)) { // window.innerHeight - borders
    callback();
    return true;
  }
};

export const setItemsAnimationDelay = ($items, delay, setToChildren = false) => {
  $items.each((i, e) => {
    if(setToChildren)
      $(e).children().css('transition-delay', ((i + 1) * delay).toString() + 'ms');
    else
      $(e).css('transition-delay', ((i + 1) * delay).toString() + 'ms');
  });
};