import Component from '../component';

class Loadable extends Component {
  setup() {
    const {
      url,
      ...options
    } = this.options;
    this.url = url;
    this.options = { ...options };
    this.$element.addClass('loadable');
  }

  fetch(filters, url) {
    this.setLoading(true);
    this.request = $.get(url || this.url, filters);
    return this.request.always(() => {
      this.setLoading(false);
    });
  }

  load(filters, url) {
    this.$element.empty();
    return this.fetch(filters, url).done((response) => {
      this.$element.html(response);
    });
  }

  append(filters, url) {
    return this.fetch(filters, url).done((response) => {
      this.$element.append(response);
    });
  }

  setLoading(newLoadingStatus) {
    if (this.isLoading !== newLoadingStatus) {
      this.isLoading = newLoadingStatus;
      if (this.isLoading) {
        this.$element.trigger('load.loadable');
        this.$element.addClass('loadable--loading');
      } else {
        this.$element.removeClass('loadable--loading');
        this.$element.trigger('loaded.loadable');
      }
    }
  }
}

Loadable.slug = 'loadable';

export default Loadable;