import Component from '../component';
import VimeoPlayer from '@vimeo/player';

const getVimeoId = (url) => {
  const regExp = /(?:www\.|player\.)?vimeo.com\/(?:channels\/(?:\w+\/)?|groups\/(?:[^\/]*)\/videos\/|album\/(?:\d+)\/video\/|video\/|)(\d+)(?:[a-zA-Z0-9_\-]+)?/;
  const match = url.match(regExp);
  return (match && match[0]) ? parseInt(match[0]) : false;
}

class VimeoVideo extends Component {
  static handlesUrl(url) {
    if (url)
      return getVimeoId(url) !== false;
  }

  setup() {
    this.url = this.options.url || this.$element.data('video-url');
  }

  listen() {
    this.$element.on('clicked-play-button.video', () => {
      this.load();
      this.player.muted = 0;
      this.player.play().then(() => {
        if (!this.options.muted && !navigator.userAgent.match(/(iPad|iPhone|iPod)/g))
          this.player.setVolume(1);
      });
    });
  }

  load(loadOptions = {}) {
    if (this.player) {
      this.player.destroy();
    }
    this.player = new VimeoPlayer(this.$element.get(0), {
      url: this.url,
      responsive: true,
      ...this.options,
      ...loadOptions,
    });
    this.player.on('loaded', () => {
      this.$element.trigger('loaded-external.video');
    });
    this.player.on('playing', () => {
      this.$element.find('.video__poster').remove();
      this.$element.trigger('playing.video');
    });
    this.player.on('ended', () => {
      this.$element.trigger('ended.video');
    });
  }

  play() {
    this.player.play();
  }

  pause() {
    this.player.pause();
  }

  unmute() {
    this.player.setMuted(false);
    this.player.setVolume(1);
  }

  destroy() {
    this.player.destroy();
    this.player = null;
    this.$element.empty();
  }
}

VimeoVideo.slug = 'video';

export default VimeoVideo;
