import $ from 'jquery';
import Component from '../component';
import Loadable from '../loadable';

class Popup extends Component {
  setup() {
    this.$bodyContent = this.$find('body-content');
  }

  listen() {
    this.$element.on('click', (event) => {
      if(event.currentTarget == event.target) {
        this.close();
      }
      event.stopPropagation();
    });

    this.$find('close-button').on('click', (event) => {
      this.close();
    });

    $(document).on('keyup', (event) => {
      if (event.key === 'Escape') 
        this.close();
    });

    this.$bodyContent.on('load.loadable', () => {
      this.$bodyContent.addClass(this.getClass('body-content', 'loading'));
    });

    this.$bodyContent.on('loaded.loadable', () => {
      this.$bodyContent.removeClass(this.getClass('body-content', 'loading'));
    });
  }

  open() {
    $('body').addClass('site--not-scrolling').addClass('site--popup-opened');
    this.$element.css('display', 'flex');
    this.$element.trigger('open.popup');
    this.$element.get(0).offsetHeight;
    this.$element.addClass('popup--opened');
    return new Promise((resolve) => {
      this.$element.one('transitionend', () => {
        if (!this.closing) {
          this.$element.trigger('opened.popup');
          this.opened = true;
          resolve();
        }
      });
    });
  }

  close() {
    if (this.opened && !this.closing) {
      $('body').removeClass('site--not-scrolling').removeClass('site--popup-opened');
      this.$element.trigger('close.popup');
      this.$element.removeClass('popup--opened');
      this.closing = new Promise((resolve) => {
        this.$element.one('transitionend', () => {
          this.$element.css('display', 'none');
          this.$element.trigger('closed.popup');
          this.closing = false;
          this.opened = false;
          resolve();
        });
      });
    }
    return this.closing;
  }

  reposition() {
    const isScrollable = this.$bodyContent.outerHeight() > this.$element.height();
    this.$element[`${isScrollable ? 'add' : 'remove'}Class`].call(
      this.$element,
      this.getClass(null, 'scrollable')
    );
  }

  loadContent(content) {
    this.$bodyContent.empty().append(content);
    this.reposition();
  }

  loadUrl(url, data) {
    if (!this.loadable) {
      this.loadable = new Loadable(this.$bodyContent);
    }
    return this.loadable.load(data, url).then(() => {
      this.reposition();
    });
  }
}

Popup.slug = 'popup';

export default Popup;
