import $ from 'jquery';

class Component {
  constructor(element, options = {}) {
    this.element = element instanceof $ ? element.get(0) : element;
    this.$element = $(element);
    this.options = options;

    this.setup();
    this.listen();
  }

  setup() {
    // implement in child
  }

  listen() {
    // implement in child
  }

  $(query) {
    return this.$element.find(query);
  }

  $find(element) {
    return this.$(this.getSelector(element));
  }

  $children(element) {
    return this.$element.children(this.getSelector(element));
  }

  $first(element) {
    return this.$(`${this.getSelector(element)}:first`);
  }

  getSelector(element) {
    return `.${this.getClass(element)}`;
  }

  getClass(element, modifier = null) {
    const elementPart = element ? `__${element}` : '';
    const modifierPart = modifier ? `--${modifier}` : '';
    return `${this.constructor.slug}${elementPart}${modifierPart}`;
  }
}

export default Component;