import Component from '../component';
import Video from '../video';

class Media extends Component {
  setup() {
    this.$object = this.$find('object');

    if (this.$object.hasClass(this.getClass('object', 'video'))) {
      if (this.$object.hasClass(this.getClass('object', 'fit-cover')))
        this.$object.width(this.$element.height() / 0.5625);
      this.video = new Video(this.$object, this.options);
    }
  }
}

Media.slug = 'media';

export default Media;
