import Component from '../component';
import YTPlayer from 'yt-player';

const getYouTubeId = (url) => {
  const regExp = /^((?:https?:)?\/\/)?((?:www|m)\.)?((?:youtube\.com|youtu.be))(\/(?:[\w\-]+\?v=|embed\/|v\/)?)([\w\-]+)(\S+)?$/;
  const match = url.match(regExp);
  return (match && match[5]) ? (match[5]) : false;
}

class YouTubeVideo extends Component {
  static handlesUrl(url) {
    if (url)
      return getYouTubeId(url) !== false;
  }

  setup() {
    this.videoId = getYouTubeId(this.options.url || this.$element.data('video-url'));
    this.$container = $("<div></div>").appendTo(this.$element).css({
      'padding': '56.25% 0 0 0',
      'position': 'relative',
    });
  }

  listen() {
    this.$element.on('clicked-play-button.video', () => {
      this.load();
      this.player.unMute();
    });
  }

  load(loadOptions = {}) {
    if (this.player) {
      this.player.destroy();
    }
    this.player = new YTPlayer($("<div></div>").appendTo(this.$container).get(0), {
      ...this.options,
      ...loadOptions,
    });
    this.player.load(this.videoId);
    this.player.on('unstarted', () => {
      this.$container.find('iframe').addClass('video__player-iframe--youtube');
      this.$element.trigger('loaded-external.video');
    });
    this.player.on('playing', () => {
      this.$element.find('.video__poster').remove();
      this.$element.trigger('playing.video');
    });
    this.player.on('ended', () => {
      this.$element.trigger('ended.video');
    });
  }

  play() {
    this.player.play();
  }

  pause() {
    this.player.pause();
  }

  unmute() {
    this.player.unMute();
  } 

  destroy() {
    this.player.destroy();
  }
}

export default YouTubeVideo;
